main {
  max-width: 960px;
  margin: 0 auto;
  padding-top: 3vmin;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  row-gap: calc(5px + 2vmin);
  text-align: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.rodada {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  column-gap: calc(5px + 2vmin);
  width: 80%;
  padding: 0 0 0 1%;
}

.rodada-nova {
  width: 60%;
  padding: 2.3vw 0 1.5vw 0;
  border-top: 35px;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}

.rodada-escolha {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  column-gap: calc(5px + 2vmin);
  transition: all 0.25s linear;
  flex-grow: 1;
}

.rodada-button, .rodada-button-new {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  flex-grow: 1;
}

.rodada-button p {
  font-size: 11vw;
  text-shadow: none;
}

.rodada-button-new p {
  font-family: "Agency FB", sans-serif;
  font-size: 9vw;
}

.rodada-button-new img {
  width: 83%;
}

.rodada-count {
  background-image: url(../images/Rodada.png);
  background-repeat: round;
  background-size: contain;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 33%;
  font-size: 14vw;
  padding: 3.7vw 0 4vw 0;
}

.nivel, .energia {
  background-image: url(../images/N_400.png);
  background-repeat: round;
  background-size: contain;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 80%;
  padding-top: 3%;
  /* max-height: 224px;/*/
  /*remover*/
  /*height: 224px; /*/
  /* border-style: solid;
  border-width: calc(0.3vmin);
  border-color: #c7c3cf; */
}

.reserva-movel {
  display: grid;
  justify-content: center;
  grid-template-columns: 1fr 2fr 1fr;
  width: 80%;
  column-gap: 20px;
  font-size: 26vw;
  align-items: center;
}

.bt-movel {
  background-color: unset;
  box-shadow: unset;
  border: unset;
  background-image: unset;
  padding-top: 20%;
}

.bt-movel img {
  width: 12vmin;
}

.reserva-conquista {
  display: grid;
  justify-content: center;
  grid-template-columns: 1fr 1fr 1fr;
  width: 51%;
  font-size: 11vw;
}

.reserva-conquista button {
  display: flex;
  align-items: center;
  justify-content: center;
}

.reserva-conquista p {
  background-color: rgba(0, 0, 0, .7);
}

.reserva-conquista img {
  width: 98%;
}

@media screen and (min-width: 500px) {
  .rodada {
    width: 400px;
    padding: 0 0 0 6px;
  }
  /* .rodada-escolha p {
    font-size: 70px;
  } */
  .rodada-nova {
    padding: 11px 0 7px 0;
  }
  .rodada-count {
    width: 131px;
    height: 114px;
    font-size: 70px;
    padding: 0 0 0 0;
  }
  .rodada-button p {
    font-size: 55px;
  }
  .rodada-button-new p {
    font-size: 45px;
  }
  .nivel, .energia {
    width: 400px;
    height: 224px;
    padding-top: 15px;
  }
  .reserva-movel {
    font-size: 130px;
  }
  .reserva-conquista {
    font-size: 55px;
  }
  .bt-movel img {
    width: unset;
  }
}