* {
  /*reset comportamento padr√£o do HTML*/
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
}

/*coloca 100% largura e altura da tela*/

html, body, #root {
  height: 100%;
}

body {
  background: #13101b;
  background-image: url(/sevengalaxies-app/static/media/treta.58d9c44d.png);
  background-size: auto auto;
  background-position: center;
  background-repeat: no-repeat;
  /*deixa a font mais legivel em brower que utiliza webkit*/
  -webkit-font-smoothing: antialiased;
}

body, input {
  /* font-family: ZagW90-Bold, Zag, sans-serif; */
  text-rendering: optimizeLegibility;
}

button, div {
  font-family: 'Open Sans', sans-serif;
  text-rendering: optimizeLegibility;
}

p {
  font-family: ZagW90-Bold, sans-serif;
  padding-left: 1vmin;
  /*margin-inline-start: 1vmin;/*/
  text-shadow: 2px 3px 8px rgba(0, 0, 0, .2);
}
main {
  max-width: 960px;
  margin: 0 auto;
  padding-top: 3vmin;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  grid-row-gap: calc(5px + 2vmin);
  row-gap: calc(5px + 2vmin);
  text-align: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.rodada {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  grid-column-gap: calc(5px + 2vmin);
  -webkit-column-gap: calc(5px + 2vmin);
          column-gap: calc(5px + 2vmin);
  width: 80%;
  padding: 0 0 0 1%;
}

.rodada-nova {
  width: 60%;
  padding: 2.3vw 0 1.5vw 0;
  border-top: 35px;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}

.rodada-escolha {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  grid-column-gap: calc(5px + 2vmin);
  -webkit-column-gap: calc(5px + 2vmin);
          column-gap: calc(5px + 2vmin);
  transition: all 0.25s linear;
  flex-grow: 1;
}

.rodada-button, .rodada-button-new {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  flex-grow: 1;
}

.rodada-button p {
  font-size: 11vw;
  text-shadow: none;
}

.rodada-button-new p {
  font-family: "Agency FB", sans-serif;
  font-size: 9vw;
}

.rodada-button-new img {
  width: 83%;
}

.rodada-count {
  background-image: url(data:image/png;base64,);
  background-repeat: round;
  background-size: contain;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 33%;
  font-size: 14vw;
  padding: 3.7vw 0 4vw 0;
}

.nivel, .energia {
  background-image: url(/sevengalaxies-app/static/media/N_400.ee0a63e6.png);
  background-repeat: round;
  background-size: contain;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 80%;
  padding-top: 3%;
  /* max-height: 224px;/*/
  /*remover*/
  /*height: 224px; /*/
  /* border-style: solid;
  border-width: calc(0.3vmin);
  border-color: #c7c3cf; */
}

.reserva-movel {
  display: grid;
  justify-content: center;
  grid-template-columns: 1fr 2fr 1fr;
  width: 80%;
  grid-column-gap: 20px;
  -webkit-column-gap: 20px;
          column-gap: 20px;
  font-size: 26vw;
  align-items: center;
}

.bt-movel {
  background-color: unset;
  box-shadow: unset;
  border: unset;
  background-image: unset;
  padding-top: 20%;
}

.bt-movel img {
  width: 12vmin;
}

.reserva-conquista {
  display: grid;
  justify-content: center;
  grid-template-columns: 1fr 1fr 1fr;
  width: 51%;
  font-size: 11vw;
}

.reserva-conquista button {
  display: flex;
  align-items: center;
  justify-content: center;
}

.reserva-conquista p {
  background-color: rgba(0, 0, 0, .7);
}

.reserva-conquista img {
  width: 98%;
}

@media screen and (min-width: 500px) {
  .rodada {
    width: 400px;
    padding: 0 0 0 6px;
  }
  /* .rodada-escolha p {
    font-size: 70px;
  } */
  .rodada-nova {
    padding: 11px 0 7px 0;
  }
  .rodada-count {
    width: 131px;
    height: 114px;
    font-size: 70px;
    padding: 0 0 0 0;
  }
  .rodada-button p {
    font-size: 55px;
  }
  .rodada-button-new p {
    font-size: 45px;
  }
  .nivel, .energia {
    width: 400px;
    height: 224px;
    padding-top: 15px;
  }
  .reserva-movel {
    font-size: 130px;
  }
  .reserva-conquista {
    font-size: 55px;
  }
  .bt-movel img {
    width: unset;
  }
}
